import { useNavigate } from "react-router-dom";
import { motion } from 'framer-motion'
import house from '../Images-icons/house.svg'
import vial from '../Images-icons/vial.png'
import resume from '../Images-icons/resume.png'

export default function MyNavbar() {
    const navigate = useNavigate()
    const slide = {
        hidden: {
            opacity: 0,
            transition: {
                when: "afterChildren"
            }
        },
        show: {
            opacity: 1,
            transition: {
                when: "beforeChildren",
                duration: 1,
                staggerChildren: .5
            },
        }
    }

    const slideChild = {
        hidden: {
            opacity: 0,
            x: 100
        },
        show: {
            opacity: 1,
            x: 0,
            transition: {
                type: "spring",
                duration: 1,
            },
        }
    }

    function handleHover(e) {
        e.target.classList.add("glitch")
    }

    function handleLeave(e) {
        e.target.classList.remove('glitch')
    }

    return (
        <motion.div
            initial="hidden"
            animate="show"
            variants={slide}
            className="nav-bar mx-auto "
        >

            <motion.p
                variants={slideChild}
                effect="Charles Cowan"
                onMouseOver={handleHover}
                onMouseLeave={handleLeave}
                onClick={()=> navigate('')}
                className=''
            >
                Charles Cowan
            </motion.p>
            <div className='nav-wrap'>
                <motion.button
                    className='btn'
                    variants={slideChild}
                >
                    <img src={house} alt='home' style={{'maxHeight':'2ch'}}/>
                    <p
                        effect="Home"
                        onMouseOver={handleHover}
                        onMouseLeave={handleLeave}
                        onClick={()=> navigate('')}
                        className=''
                    >
                        Home
                    </p>
                </motion.button>
                <motion.button
                    className='btn'
                    variants={slideChild}
                >
                    <img src={vial} alt='expirements' style={{'maxHeight':'2ch'}}/>
                    <p
                        effect="Expirements"
                        onMouseOver={handleHover}
                        onMouseLeave={handleLeave}
                        onClick={()=> navigate('expirements')}
                        className=''
                    >
                        Expirements
                    </p>
                </motion.button>
                <motion.button
                    className='btn'
                    variants={slideChild}
                >
                    <img src={resume} alt='resume' style={{'maxHeight':'2ch'}}/>
                    <p
                        effect="Resume"
                        onMouseOver={handleHover}
                        onMouseLeave={handleLeave}
                        onClick={() => navigate('resume')}
                        className=''
                    >
                        Resume
                    </p>
                </motion.button>
            </div>
        </motion.div>
    )
}
